/* ********************************************************************
  (1) GRAPHIC STYLE (Play around in this section)
********************************************************************* */
/* GLOBAL FONT */
.mfp-container,
.media-box,
.media-boxes-load-more-button,
.media-boxes-filter,
.media-boxes-drop-down-menu > li > a, .media-boxes-drop-down-header {
  /*font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;*/
  font-family: 'Open Sans'; }

/* ====================================================================== *
      [1] MEDIA BOX CONTENT
 * ====================================================================== */
.media-box {
  font-size: 13px; }

.media-box iframe {
  display: block;
  margin: 0 !important; }

.media-box-container {
  border-radius: 3px;
  -o-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  -ms-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15); }

.media-box-content {
  padding: 20px;
  position: relative;
  background: white;
  color: #333333;
  line-height: 17px; }

.thumbnail-overlay-animated {
  position: relative;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out; }

.thumbnail-overlay-animated[data-from="top"] {
  top: -20px; }

.thumbnail-overlay-animated[data-from="bottom"] {
  top: 20px; }

.thumbnail-overlay-animated[data-from="left"] {
  left: -20px; }

.thumbnail-overlay-animated[data-from="right"] {
  left: 20px; }

/* desktop */
@media only screen and (min-width: 768px) {
  .thumbnail-overlay:hover .thumbnail-overlay-animated {
    opacity: 1;
    left: 0;
    top: 0; } }

/* mobile */
@media only screen and (max-width: 768px) {
  .media-box-image:hover .thumbnail-overlay-animated {
    opacity: 1;
    left: 0;
    top: 0; } }

.thumbnail-overlay-button {
  display: inline-block;
  color: white;
  font-size: 13px;
  background: #e54e53;
  background: #e54e53;
  padding: 6px 8px;
  margin: 3px 0;
  -webkit-transition: background 0.1s ease-in-out;
  transition: background 0.1s ease-in-out; }

.thumbnail-overlay-button:hover {
  background: rgba(229, 78, 83, 0.8); }

.thumbnail-overlay-caption {
  display: inline-block;
  color: white;
  font-size: 11px;
  background: rgba(255, 255, 255, 0.2);
  padding: 4px 6px;
  margin: 3px 0; }

.media-box-title {
  letter-spacing: 0px;
  line-height: 1.1;
  font-weight: 800;
  color: #333333;
  font-size: 14px; }

.media-box-title small {
  font-size: 12px;
  display: block;
  font-weight: 300;
  margin-top: 5px; }

.media-box-year {
  color: gray;
  font-size: 11px;
  margin-bottom: 3px; }

.media-box-author {
  font-size: 11px;
  margin: 3px 0 0px 0 !important; }

.media-box-categories {
  font-size: 11px;
  line-height: 15px;
  color: #e54e53;
  margin-top: 5px; }

.media-box-categories span {
  color: #333333;
  font-weight: 600; }

.media-box-date {
  font-size: 11px;
  line-height: 15px;
  color: #e54e53;
  margin-top: 5px; }

.media-box-date span {
  color: #333333;
  font-weight: 600; }

.media-box-big-icon {
  margin-bottom: 12px; }

.media-box-big-icon .fa {
  font-size: 55px !important;
  color: white !important;
  -webkit-transform: scale(1) !important;
  -ms-transform: scale(1) !important;
  transform: scale(1) !important; }

.media-box-text {
  font-size: 12px;
  margin: 13px 0px 13px 0 !important;
  color: #333333; }

.media-box-quote {
  padding: 30px;
  background: #e54e53;
  color: #fff;
  font-size: 14px;
  line-height: 28px;
  font-weight: 600; }

.media-box-quote span {
  display: block;
  margin-top: 20px;
  font-size: 13px; }

.media-box-subscribe {
  padding: 30px;
  background: #2C393F;
  color: #fff;
  font-size: 14px;
  line-height: 28px;
  font-weight: 600; }

.media-box-subscribe p {
  color: #fff; }

.media-box-subscribe input {
  border-radius: 0px;
  border: 0;
  margin-top: 10px;
  margin-bottom: 15px; }

.media-box-subscribe .btn {
  border: 0;
  color: #fff;
  background: #86979F;
  border-radius: 2px;
  box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.15);
  min-width: 120px;
  font-weight: 500;
  font-size: 12px;
  padding: 12px; }

.media-box-view-image {
  margin: 12px 0 0 0; }

.media-box-more a, .media-box-view-image a {
  text-decoration: none !important;
  color: inherit;
  padding: 0;
  margin: 0;
  color: #e54e53;
  font-weight: 600;
  font-size: 11px; }

.media-box-more a:hover, .media-box-view-image a:hover {
  color: #333333; }

a.media-box-read-more-link {
  background: #e54e53;
  color: #fff;
  padding: 0 4px; }

a.media-box-read-more-link:hover {
  background: #333333;
  text-decoration: none; }

.media-box-footer {
  background: #F8F8F8;
  padding: 20px; }

.media-box-social-buttons {
  text-align: center; }

.media-box-social-buttons i {
  font-size: 15px;
  color: #C7C7C7;
  margin: 0 7px;
  -webkit-transition: color 0.1s;
  transition: color 0.1s; }

.media-box-social-buttons i:hover {
  color: #333; }

.media-box-play-button {
  background: rgba(255, 255, 255, 0.8) !important;
  color: #000 !important;
  padding-left: 3px; }

.media-box-sound-button {
  background: rgba(255, 255, 255, 0.8) !important;
  color: #000 !important;
  padding-left: 1px; }

.media-box-logo {
  text-align: center;
  font-size: 80px;
  padding: 30px 0;
  color: rgba(255, 255, 255, 0.7);
  -webkit-transition: color 0.1s;
  transition: color 0.1s; }

.media-box-logo:hover {
  color: white; }

.media-box-price {
  font-size: 16px;
  color: #e54e53;
  margin-top: 10px;
  font-weight: 400; }

.media-box-price del {
  font-size: 12px;
  color: #A4A4A4; }

.media-box-sale {
  color: #fff;
  background: #e54e53;
  width: 45px;
  height: 45px;
  line-height: 45px;
  border-radius: 50%;
  text-align: center;
  position: absolute;
  font-weight: 100;
  font-size: 12px;
  top: -10px;
  right: 10px;
  z-index: 99; }

/* ====================================================================== *
      [2] LOAD MORE
 * ====================================================================== */
.media-boxes-load-more-button {
  cursor: pointer;
  width: 150px;
  text-align: center;
  color: white;
  background-color: #333333;
  font-size: 14px !important;
  height: 15px;
  padding: 10px 15px 10px 15px;
  margin: 0px auto;
  line-height: 15px;
  -o-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  -ms-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  box-sizing: content-box !important; }

.media-boxes-no-more-entries {
  filter: alpha(opacity=20);
  opacity: 0.2; }

/* ====================================================================== *
      [3] FILTER BAR
 * ====================================================================== */
.filters-container {
  margin-bottom: 15px; }

.filters-container:after {
  content: " ";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both; }

@media only screen and (max-width: 768px) {
  .filters-container > * {
    float: left !important;
    display: block;
    margin-bottom: 5px !important;
    margin-right: 5px !important; }
  .filters-container .media-boxes-filter {
    margin-top: -5px !important; }
  .filters-container .media-boxes-filter > li {
    margin-top: 5px !important; } }

.media-boxes-filter {
  font-size: 0px;
  list-style: none;
  margin: 0;
  padding: 0px;
  text-align: left; }

.media-boxes-filter > li {
  display: inline-block;
  line-height: 16px;
  margin: 0 8px 0 0;
  /* Safari/Chrome, other WebKit */
  /* Firefox, other Gecko */
  box-sizing: border-box;
  /* Opera/IE 8+ */ }

.media-boxes-filter > li:last-child {
  background: none; }

.media-boxes-filter > li a {
  display: block;
  cursor: pointer;
  color: #676767;
  font-size: 12px;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0px 0px 0;
  padding: 11px 14px;
  background-color: white;
  border-radius: 1px;
  -webkit-transition: background-color 0.1s;
  transition: background-color 0.1s;
  -o-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  -ms-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2); }

.media-boxes-filter > li a:hover {
  text-decoration: none;
  background: #333333;
  color: #fff; }

.media-boxes-filter > li a.selected {
  background: #e54e53 !important;
  color: #fff !important;
  box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.15) !important; }

/* ====================================================================== *
      [4] SEARCH FILTER
 * ====================================================================== */
.media-boxes-search {
  padding: 9px 14px 9px 14px;
  font-size: 12px;
  margin: 0;
  font-weight: 400;
  color: #555;
  vertical-align: middle;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 1px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  box-sizing: border-box; }

.media-boxes-search:focus {
  border-color: #66afe9;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6); }

/* ====================================================================== *
      [5] DROP DOWN MENU
 * ====================================================================== */
.media-boxes-drop-down {
  width: 140px;
  margin: 0;
  position: relative;
  -o-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  -ms-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2); }

.media-boxes-drop-down-header .fa {
  float: right; }

.media-boxes-drop-down-menu {
  margin: 0;
  padding: 0;
  z-index: 99999;
  position: absolute;
  width: 100%;
  display: none;
  -o-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  -ms-box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2);
  box-shadow: 0 1px 1px rgba(34, 25, 25, 0.2); }

.media-boxes-drop-down-menu > li {
  list-style: none; }

.media-boxes-drop-down-menu > li:first-child > a {
  border-top: 1px solid #E9E9E9; }

.media-boxes-drop-down-menu > li > a, .media-boxes-drop-down-header {
  font-size: 12px;
  font-weight: 400;
  display: block;
  padding: 10px 14px;
  text-decoration: none;
  background-color: white;
  color: #676767;
  -ms-box-sizing: border-box;
  box-sizing: border-box; }

.media-boxes-drop-down-menu > li > a {
  color: #999999; }

.media-boxes-drop-down-menu > li > a:hover, .media-boxes-drop-down-menu > li > a:focus {
  color: #000; }

.media-boxes-drop-down-menu > li.selected > a,
.media-boxes-drop-down-menu > li > a.selected {
  color: #e54e53; }

.media-boxes-drop-down-header {
  background-color: white;
  cursor: pointer; }

/* ====================================================================== *
      [6] THUMBNAIL OVERLAY EFFECT
 * ====================================================================== */
